}

.rounded-m3-full {
  border-radius: var(--radius-xl) !important;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ========================================
   Body & Theme Classes
   ======================================== */
body {
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background: var(--bg-main);
  color: var(--text-primary);
  letter-spacing: -0.01em;
  transition: var(--theme-transition);
}

/* Theme-aware elements with smooth transitions */
body.theme-dark,
body.theme-light {
  transition: var(--theme-transition);
}

/* ========================================
   OLED Theme - Less rounded, cleaner look for pure black theme
   ======================================== */
body.theme-oled {
  --radius-sm: 4px;
  --radius-md: 6px;
  --radius-lg: 8px;
  --radius-xl: 12px;
  --m3-shape-xs: 2px;
  --m3-shape-sm: 4px;
  --m3-shape-md: 6px;
  --m3-shape-lg: 8px;
  --m3-shape-xl: 12px;
}

body.theme-oled .hover-card {
  border-radius: 6px;
}

body.theme-oled .rounded-xl {
  border-radius: 8px !important;
}

body.theme-oled .rounded-2xl {
  border-radius: 10px !important;
}

body.theme-oled .rounded-full {
  border-radius: 6px !important;
}

/* ========================================
   Light Theme - Colors are set dynamically by m3ThemeEngine
   No hardcoded colors - all derived from M3 variables
   ======================================== */

/* Dynamic scrollbar using M3 outline colors */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--m3-outline-variant);
  border-radius: 4px;
  opacity: 0.6;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--m3-outline);
}

/* Apply transitions to common theme-affected elements */
.theme-transition,
[style*="--bg"],
[style*="background"],
.sidebar-item,
.track-row,
.hover-card,
.search-bar,
.search-input,
.glass,
.glass-button {
  transition: var(--theme-transition);
}

/* Glassmorphism base - Dynamic colors */
.glass {
  background: color-mix(in srgb, var(--m3-surface) 85%, transparent);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--m3-outline-variant);
}

.glass-button {
  background: color-mix(in srgb, var(--m3-surface-container-high) 80%, transparent);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.glass-button:hover {
  background: color-mix(in srgb, var(--m3-surface-container-highest) 90%, transparent);
}

/* Search Bar - Dynamic */
.search-bar {
  position: sticky;
  top: 0;
  z-index: 10;
  background: color-mix(in srgb, var(--m3-surface) 90%, transparent);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  padding: 20px;
  border-bottom: 1px solid var(--m3-outline-variant);
}

.search-input {
  width: 100%;
  max-width: 400px;
  background: var(--m3-surface-container-high);
  border: 1px solid transparent;
  border-radius: var(--radius-md);
  padding: 12px 16px 12px 44px;
  color: var(--m3-on-surface);
  font-size: 14px;
  font-weight: 400;
  transition: all 0.2s ease;
}

.search-input:focus {
  outline: none;
  border-color: var(--m3-primary);
  background: var(--m3-surface-container-high);
}

.search-input::-moz-placeholder {
  color: var(--m3-on-surface-variant);
}

.search-input::placeholder {
  color: var(--m3-on-surface-variant);
}

/* Progress Bar Styling - Dynamic */
input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background: transparent;
  cursor: pointer;
  height: 4px;
}

input[type="range"]::-webkit-slider-runnable-track {
  background: var(--m3-surface-container-highest);
  height: 4px;
  border-radius: 2px;
  -webkit-transition: height 0.2s ease;
  transition: height 0.2s ease;
}

input[type="range"]:hover::-webkit-slider-runnable-track {
  height: 6px;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: var(--m3-primary);
  height: 0px;
  width: 0px;
  border-radius: 50%;
  margin-top: -1px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px var(--m3-shadow);
}

input[type="range"]:hover::-webkit-slider-thumb {
  height: 14px;
  width: 14px;
  margin-top: -4px;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(16px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes nowPlaying {

  0%,
  100% {
    height: 4px;
  }

  50% {
    height: 16px;
  }
}

@keyframes vibeIn {
  from {
    opacity: 0;
    backdrop-filter: blur(0px);
  }

  to {
    opacity: 1;
    backdrop-filter: blur(100px);
  }
}

@keyframes albumArtPulse {

  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.02);
  }
}

@keyframes pageTransition {
  from {
    opacity: 0;
    transform: translateX(8px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-fade-in {
  animation: fadeIn 0.25s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.animate-slide-up {
  animation: slideUp 0.35s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.animate-vibe-in {
  animation: vibeIn 0.4s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

.album-art-vibe {
  animation: albumArtPulse 4s ease-in-out infinite;
}

.animate-page {
  animation: pageTransition 0.3s cubic-bezier(0.4, 0, 0.2, 1) forwards;
}

/* View transition animation for MainContent */
.view-transition {
  animation: viewFade 0.15s ease-out forwards;
  will-change: opacity;
}

@keyframes viewFade {
  from {
    opacity: 0.7;
  }

  to {
    opacity: 1;
  }
}

/* View content padding for player bar */
.view-content {
  padding-bottom: 120px;
}

/* Hover card animation */
.hover-card {
  transition: all 0.25s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: var(--radius-md);
}

.hover-card:hover {
  transform: translateY(-2px);
  background: var(--bg-hover);
}

/* Play button animation */
.play-btn {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateY(8px);
  opacity: 0;
}

.hover-card:hover .play-btn {
  transform: translateY(0);
  opacity: 1;
}

.play-btn:hover {
  transform: scale(1.1) !important;
}

.play-btn:active {
  transform: scale(0.95) !important;
}

/* Sidebar item animation */
.sidebar-item {
  transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  border-radius: var(--radius-md);
}

.sidebar-item::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%) scaleY(0);
  width: 3px;
  height: 60%;
  background: var(--color-primary);
  border-radius: 0 3px 3px 0;
  transition: transform 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

.sidebar-item.active::before {
  transform: translateY(-50%) scaleY(1);
}

/* Button press effect */
.btn-press {
  transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-press:active {
  transform: scale(0.92);
}

/* Glow effect - Uses M3 primary color */
.glow-primary {
  box-shadow: 0 0 20px color-mix(in srgb, var(--m3-primary) 40%, transparent);
}

.glow-primary:hover {
  box-shadow: 0 0 30px color-mix(in srgb, var(--m3-primary) 60%, transparent);
}

/* Gradient text - Uses M3 primary and tertiary */
.gradient-text {
  background: linear-gradient(135deg, var(--m3-primary), var(--m3-tertiary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Track row hover */
.track-row {
  transition: all 0.15s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: var(--radius-sm);
}

.track-row:hover {
  background: var(--m3-surface-container-high);
}

/* Now playing indicator */
.now-playing-bar {
  animation: nowPlaying 0.5s ease-in-out infinite;
}

.now-playing-bar:nth-child(2) {
  animation-delay: 0.1s;
}

.now-playing-bar:nth-child(3) {
  animation-delay: 0.2s;
}

/* Volume slider container */
.volume-slider {
  width: 0;
  overflow: hidden;
  transition: width 0.3s ease;
}

.volume-container:hover .volume-slider {
  width: 100px;
}

/* Skeleton loading - Uses M3 surface colors */
.skeleton {
  background: linear-gradient(90deg, var(--m3-surface-container) 25%, var(--m3-surface-container-high) 50%, var(--m3-surface-container) 75%);
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% {
    background-position: 200% 0;
  }

  100% {
    background-position: -200% 0;
  }
}

/* Player backdrop - Uses M3 surface colors */
.player-backdrop {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  background: color-mix(in srgb, var(--m3-surface-container) 90%, transparent);
  border-top: 1px solid var(--m3-outline-variant);
}

/* Smooth transitions */
.smooth-color {
  transition: color 0.2s cubic-bezier(0.4, 0, 0.2, 1),
    background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
}

/* ========================================
   Material Design 3 Components
   ======================================== */

/* M3 FAB (Floating Action Button) */
.m3-fab {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
}

.m3-fab-small {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-md);
}

.m3-fab-medium {
  width: 56px;
  height: 56px;
  border-radius: var(--radius-lg);
}

.m3-fab-large {
  width: 96px;
  height: 96px;
  border-radius: var(--radius-xl);
}

.m3-fab-extended {
  height: 56px;
  padding: 0 20px;
  border-radius: var(--radius-lg);
  gap: 12px;
}

.m3-fab-primary {
  background: var(--m3-primary-container);
  color: var(--m3-on-primary-container);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 4px 8px 3px rgba(0, 0, 0, 0.15);
}

.m3-fab-primary:hover {
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.3), 0 6px 10px 4px rgba(0, 0, 0, 0.15);
}

.m3-fab-secondary {
  background: var(--m3-secondary-container);
  color: var(--m3-on-secondary-container);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 4px 8px 3px rgba(0, 0, 0, 0.15);
}

.m3-fab-tertiary {
  background: var(--m3-tertiary-container);
  color: var(--m3-on-tertiary-container);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 4px 8px 3px rgba(0, 0, 0, 0.15);
}

.m3-fab-surface {
  background: var(--m3-surface-container-high);
  color: var(--m3-primary);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3), 0 4px 8px 3px rgba(0, 0, 0, 0.15);
}

/* M3 Pill Button */
.m3-pill-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  padding: 0 24px;
  border-radius: var(--radius-xl);
  border: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 14px;
  gap: 8px;
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
}

.m3-pill-filled {
  background: var(--m3-primary);
  color: var(--m3-on-primary);
}

.m3-pill-filled:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3), 0 1px 3px 1px rgba(0, 0, 0, 0.15);
}

.m3-pill-tonal {
  background: var(--m3-secondary-container);
  color: var(--m3-on-secondary-container);
}

.m3-pill-outlined {
  background: transparent;
  color: var(--m3-primary);
  border: 1px solid var(--m3-outline);
}

/* M3 Icon Button */
.m3-icon-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: var(--radius-xl);
  border: none;
  background: transparent;
  cursor: pointer;
  color: var(--m3-on-surface-variant);
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
}

.m3-icon-button:hover {
  background: rgba(var(--m3-on-surface-variant), 0.08);
}

.m3-icon-button-filled {
  background: var(--m3-primary);
  color: var(--m3-on-primary);
}

.m3-icon-button-tonal {
  background: var(--m3-secondary-container);
  color: var(--m3-on-secondary-container);
}

/* M3 Card */
.m3-card {
  border-radius: var(--radius-xl);
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
}

.m3-card-elevated {
  background: var(--m3-surface-container-low);
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3), 0 1px 3px 1px rgba(0, 0, 0, 0.15);
}

.m3-card-elevated:hover {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.3), 0 2px 6px 2px rgba(0, 0, 0, 0.15);
}

.m3-card-filled {
  background: var(--m3-surface-container-highest);
}

.m3-card-outlined {
  background: var(--m3-surface);
  border: 1px solid var(--m3-outline-variant);
}

/* M3 Surface Elevation */
.m3-surface-1 {
  background: var(--m3-surface-container-lowest);
}

.m3-surface-2 {
  background: var(--m3-surface-container-low);
}

.m3-surface-3 {
  background: var(--m3-surface-container);
}

.m3-surface-4 {
  background: var(--m3-surface-container-high);
}

.m3-surface-5 {
  background: var(--m3-surface-container-highest);
}

/* M3 Navigation Rail / Bottom Nav */
.m3-nav-rail {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 12px 0;
  background: var(--m3-surface-container);
  gap: 4px;
}

.m3-nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 56px;
  min-height: 56px;
  padding: 4px 0;
  gap: 4px;
  cursor: pointer;
  border-radius: 16px;
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
  color: var(--m3-on-surface-variant);
}

.m3-nav-item:hover {
  background: rgba(var(--m3-on-surface), 0.08);
}

.m3-nav-item.active {
  color: var(--m3-on-secondary-container);
}

.m3-nav-item.active .m3-nav-indicator {
  background: var(--m3-secondary-container);
}

.m3-nav-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 32px;
  border-radius: 16px;
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
}

.m3-nav-label {
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* M3 Bottom Navigation */
.m3-bottom-nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  background: var(--m3-surface-container);
  border-top: 1px solid var(--m3-outline-variant);
}

.m3-bottom-nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  height: 100%;
  gap: 4px;
  cursor: pointer;
  color: var(--m3-on-surface-variant);
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
}

.m3-bottom-nav-item.active {
  color: var(--m3-on-secondary-container);
}

.m3-bottom-nav-item.active .m3-bottom-nav-indicator {
  background: var(--m3-secondary-container);
}

.m3-bottom-nav-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 32px;
  border-radius: 16px;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
}

/* M3 Search Bar */
.m3-search-bar {
  display: flex;
  align-items: center;
  height: 56px;
  padding: 0 16px;
  background: var(--m3-surface-container-high);
  border-radius: 28px;
  gap: 16px;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
}

.m3-search-bar:focus-within {
  background: var(--m3-surface-container-highest);
}

.m3-search-input {
  flex: 1;
  height: 100%;
  border: none;
  background: transparent;
  font-size: 16px;
  color: var(--m3-on-surface);
}

.m3-search-input::-moz-placeholder {
  color: var(--m3-on-surface-variant);
}

.m3-search-input::placeholder {
  color: var(--m3-on-surface-variant);
}

.m3-search-input:focus {
  outline: none;
}

/* M3 Ripple Effect */
.m3-ripple {
  position: relative;
  overflow: hidden;
}

.m3-ripple::after {
  content: '';
  position: absolute;
  inset: 0;
  background: currentColor;
  opacity: 0;
  transition: opacity 0.2s;
}

.m3-ripple:hover::after {
  opacity: 0.08;
}

.m3-ripple:active::after {
  opacity: 0.12;
}

/* Scrollbar styling - Uses M3 outline colors */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--m3-outline-variant);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--m3-outline);
}

/* Focus visible for accessibility */
button:focus-visible,
input:focus-visible {
  outline: 2px solid var(--m3-primary);
  outline-offset: 2px;
}

