\n  --m3-shape-xs: 4px;\n  --m3-shape-sm: 8px;\n  --m3-shape-md: 12px;\n  --m3-shape-lg: 16px;\n  --m3-shape-xl: 28px;\n\n  /* M3 Default Colors (overridden by theme engine) */\n  --m3-primary: #d0bcff;\n  --m3-on-primary: #381e72;\n  --m3-primary-container: #4f378b;\n  --m3-on-primary-container: #eaddff;\n\n  --m3-secondary: #ccc2dc;\n  --m3-on-secondary: #332d41;\n  --m3-secondary-container: #4a4458;\n  --m3-on-secondary-container: #e8def8;\n\n  --m3-tertiary: #efb8c8;\n  --m3-on-tertiary: #492532;\n  --m3-tertiary-container: #633b48;\n  --m3-on-tertiary-container: #ffd8e4;\n\n  --m3-error: #f2b8b5;\n  --m3-on-error: #601410;\n  --m3-error-container: #8c1d18;\n  --m3-on-error-container: #f9dedc;\n\n  --m3-surface: #141218;\n  --m3-on-surface: #e6e1e5;\n  --m3-surface-variant: #49454f;\n  --m3-on-surface-variant: #cac4d0;\n  --m3-surface-container-lowest: #0f0d13;\n  --m3-surface-container-low: #1d1b20;\n  --m3-surface-container: #211f26;\n  --m3-surface-container-high: #2b2930;\n  --m3-surface-container-highest: #36343b;\n\n  --m3-outline: #938f99;\n  --m3-outline-variant: #49454f;\n\n  --m3-inverse-surface: #e6e1e5;\n  --m3-inverse-on-surface: #313033;\n  --m3-inverse-primary: #6750a4;\n\n  --m3-shadow: #000000;\n  --m3-scrim: #000000;\n\n  --m3-background: #141218;\n  --m3-on-background: #e6e1e5;\n\n  /* M3 State Layers */\n  --m3-state-hover: 0.08;\n  --m3-state-focus: 0.12;\n  --m3-state-pressed: 0.12;\n  --m3-state-dragged: 0.16;\n\n  /* Theme Transition */\n  --theme-transition: background-color 0.3s ease, color 0.2s ease, border-color 0.3s ease;\n  --theme-transition-duration: 300ms;\n}\n\n/* ========================================\n   Dynamic Radius Utility Classes\n   ======================================== */\n.radius-sm {\n  border-radius: var(--radius-sm) !important;\n}\n\n.radius-md {\n  border-radius: var(--radius-md) !important;\n}\n\n.radius-lg {\n  border-radius: var(--radius-lg) !important;\n}\n\n.radius-xl {\n  border-radius: var(--radius-xl) !important;\n}\n\n.radius-full {\n  border-radius: var(--radius-xl) !important;\n}\n\n/* Override Tailwind rounded classes to use dynamic values */\n.rounded-sm {\n  border-radius: var(--radius-sm) !important;\n}\n\n.rounded {\n  border-radius: var(--radius-sm) !important;\n}\n\n.rounded-md {\n  border-radius: var(--radius-md) !important;\n}\n\n.rounded-lg {\n  border-radius: var(--radius-lg) !important;\n}\n\n.rounded-xl {\n  border-radius: var(--radius-lg) !important;\n}\n\n.rounded-2xl {\n  border-radius: var(--radius-xl) !important;\n}\n\n/* Dynamic full radius - scales with setting but doesn't go full circle */\n.rounded-full {\n  border-radius: var(--radius-xl) !important;\n}\n\n/* Exception: Keep true circles for specific elements like avatars */\n.rounded-circle {\n  border-radius: 50% !important;\n}\n\n/* Album/Playlist cover images */\n.album-cover,\n.playlist-cover,\n.cover-image {\n  border-radius: var(--radius-md);\n  transition: border-radius 0.3s ease;\n}\n\n/* Play/Pause button - main control */\n.play-pause-btn {\n  border-radius: var(--radius-xl);\n  transition: border-radius 0.3s ease, transform 0.2s ease;\n}\n\n/* Control buttons (skip, shuffle, repeat) */\n.control-btn {\n  border-radius: var(--radius-lg);\n  transition: border-radius 0.3s ease;\n}\n\n/* Cards and containers */\n.dynamic-card {\n  border-radius: var(--radius-lg);\n  transition: border-radius 0.3s ease;\n}\n\n/* M3 Shape overrides */\n.rounded-m3-sm {\n  border-radius: var(--radius-sm) !important;\n}\n\n.rounded-m3-md {\n  border-radius: var(--radius-md) !important;\n}\n\n.rounded-m3-lg {\n  border-radius: var(--radius-lg) !important;\n}\n\n.rounded-m3-xl {\n  border-radius: var(--radius-xl) !important;\n}\n\n.rounded-m3-full {\n  border-radius: var(--radius-xl) !important;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n/* ========================================\n   Body & Theme Classes\n   ======================================== */\nbody {\n  font-family: var(--font-family);\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  overflow: hidden;\n  background: var(--bg-main);\n  color: var(--text-primary);\n  letter-spacing: -0.01em;\n  transition: var(--theme-transition);\n}\n\n/* Theme-aware elements with smooth transitions */\nbody.theme-dark,\nbody.theme-light {\n  transition: var(--theme-transition);\n}\n\n/* ========================================\n   OLED Theme - Less rounded, cleaner look for pure black theme\n   ======================================== */\nbody.theme-oled {\n  --radius-sm: 4px;\n  --radius-md: 6px;\n  --radius-lg: 8px;\n  --radius-xl: 12px;\n  --m3-shape-xs: 2px;\n  --m3-shape-sm: 4px;\n  --m3-shape-md: 6px;\n  --m3-shape-lg: 8px;\n  --m3-shape-xl: 12px;\n}\n\nbody.theme-oled .hover-card {\n  border-radius: 6px;\n}\n\nbody.theme-oled .rounded-xl {\n  border-radius: 8px !important;\n}\n\nbody.theme-oled .rounded-2xl {\n  border-radius: 10px !important;\n}\n\nbody.theme-oled .rounded-full {\n  border-radius: 6px !important;\n}\n\n/* ========================================\n   Light Theme - Colors are set dynamically by m3ThemeEngine\n   No hardcoded colors - all derived from M3 variables\n   ======================================== */\n\n/* Dynamic scrollbar using M3 outline colors */\n::-webkit-scrollbar {\n  width: 8px;\n}\n\n::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  background: var(--m3-outline-variant);\n  border-radius: 4px;\n  opacity: 0.6;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: var(--m3-outline);\n}\n\n/* Apply transitions to common theme-affected elements */\n.theme-transition,\n[style*=\"--bg\"],\n[style*=\"background\"],\n.sidebar-item,\n.track-row,\n.hover-card,\n.search-bar,\n.search-input,\n.glass,\n.glass-button {\n  transition: var(--theme-transition);\n}\n\n/* Glassmorphism base - Dynamic colors */\n.glass {\n  background: color-mix(in srgb, var(--m3-surface) 85%, transparent);\